@font-face {
  font-family: "Cyberthrone";
  src: url("../app/fonts/cyberthrone.woff2");
}
:root {
  --font-body: "Cyberthrone";
  --transition-min: .2s;
  --transition-max: 8s;
  --animate-delay: 1s ;
}

:root {
  --white: hsl(0, 0%, 100%);
  --aqua: hsl(179, 100%, 50%);
  --cherry: hsl(327, 88%, 37%);
  --light-gray: hsl(0, 0%, 20%);
  --dark-gray: hsl(0, 0%, 8%);
  --black: hsl(0, 0%, 0%);
}

.animated {
  animation-duration: 1s;
  animation-fill-mode: both;
}

.delay-1 {
  animation-delay: var(--animate-delay);
}

.delay-2 {
  animation-delay: calc(var(--animate-delay) * 1.2);
}

.delay-3 {
  animation-delay: calc(var(--animate-delay) * 1.3);
}

.delay-4 {
  animation-delay: calc(var(--animate-delay) * 1.4);
}

.delay-5 {
  animation-delay: calc(var(--animate-delay) * 1.5);
}

.delay-6 {
  animation-delay: calc(var(--animate-delay) * 1.6);
}

.delay-7 {
  animation-delay: calc(var(--animate-delay) * 1.7);
}

.delay-8 {
  animation-delay: calc(var(--animate-delay) * 1.8);
}

.delay-9 {
  animation-delay: calc(var(--animate-delay) * 1.9);
}

.delay-10 {
  animation-delay: calc(var(--animate-delay) * 2);
}

.delay-11 {
  animation-delay: calc(var(--animate-delay) * 2.1);
}

.delay-12 {
  animation-delay: calc(var(--animate-delay) * 2.2);
}

.delay-13 {
  animation-delay: calc(var(--animate-delay) * 2.3);
}

.delay-14 {
  animation-delay: calc(var(--animate-delay) * 2.4);
}

.delay-15 {
  animation-delay: calc(var(--animate-delay) * 2.5);
}

.fadeIn {
  animation-name: fadeIn;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  outline: none;
}

body {
  margin: 0;
  font-size: 1.875rem;
  font-family: var(--font-body);
  color: var(--white);
  line-height: 1.3;
  cursor: url("../images/tools/normal.cur"), auto;
}
@media (min-width: 71.875em) {
  body {
    overflow: hidden;
  }
}

.wb-body {
  background-color: var(--dark-gray);
  color: var(--white);
  padding: 20px;
}

#click {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 1;
  background-color: var(--black);
}
#click::after {
  content: "";
  width: 37.5rem;
  min-height: 20.9375rem;
  background: url("../images/loading.webp") no-repeat 50%/contain;
}

.section__title, .section__subtitle {
  cursor: url("../images/tools/busy.cur"), auto;
}
.section__links {
  display: inline-flex;
}
.section__links:first-child a {
  color: var(--cherry);
}
.section__links:first-child a:not(:last-child) {
  padding-right: 1.25rem;
}
.section__links:last-child {
  flex-direction: column;
}

h1, h2, h3, p {
  margin: 0;
}

a, a:visited, a:active {
  text-decoration: none;
  color: var(--aqua);
}

a:hover, #click {
  cursor: url("../images/tools/link.cur"), auto;
  text-decoration: line-through;
}

.section__links:first-child a {
  text-decoration-color: var(--aqua);
  text-shadow: 0 0 0.625rem var(--cherry);
}
.section__links:last-child a {
  text-decoration-color: var(--cherry);
  text-shadow: 0 0 0.625rem var(--aqua);
}

.wrapper {
  height: 100vw;
}
@media (min-width: 71.875em) {
  .wrapper {
    transform: rotate(-90deg) translateX(-100vh);
    transform-origin: top left;
    overflow-x: scroll;
  }
  .wrapper::-webkit-scrollbar {
    display: none;
  }
  .wrapper__inner {
    display: flex;
    transform: rotate(90deg) translateY(-100vh);
    transform-origin: top left;
  }
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  position: absolute;
  min-width: 100%;
  overflow: hidden;
}
@media (min-width: 71.875em) {
  .container {
    flex-direction: row;
    align-items: center;
  }
  .container__portfolio {
    justify-content: flex-end;
  }
}

.slide {
  display: flex;
  flex-direction: column;
  background-image: url("../images/background.webp");
  max-height: 100vh;
}
.slide.one {
  align-items: center;
  justify-content: center;
}
.slide.two {
  align-items: flex-end;
  background-image: none;
  background-color: var(--light-gray);
}
@media (min-width: 71.875em) {
  .slide.two {
    box-shadow: -8.75rem 0 2.5rem 2.8125rem var(--dark-gray);
  }
}
.slide::before {
  content: "";
  min-width: 100vw;
  min-height: 100vh;
  background-color: rgba(0, 0, 0, 0.6);
}

.hidden {
  position: relative;
  left: -100%;
}

.one .section {
  padding: 31.25rem 0 0 3.125rem;
}
@media (min-width: 71.875em) {
  .one .section {
    padding: 0;
  }
  .one .section__title {
    font-size: 9.375rem;
  }
  .one .section__subtitle {
    font-size: 2.875rem;
    margin-bottom: 3.4375rem;
  }
}
.section__scroll {
  overflow: visible;
  max-width: 71.25rem;
}
@media (min-width: 71.875em) {
  .section__scroll {
    overflow: scroll;
  }
}
.section__scroll::-webkit-scrollbar {
  display: none;
}
.section__title {
  position: relative;
  font-size: 7.8125rem;
  margin: 0.3125rem 0;
  transform: translate(0%, 0%);
  line-height: 0.8;
}
.section__title:before, .section__title:after {
  content: "codlipa";
  position: absolute;
  left: 0;
  opacity: 0.8;
}
.section__title:after {
  color: var(--cherry);
  z-index: -2;
}
.section__title:before {
  color: var(--aqua);
  z-index: -1;
}
.section__title:hover:before, .section__title:hover:after {
  animation: glitch var(--transition-min) cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite;
}
.section__title:hover:after {
  animation-direction: reverse;
}
.section__subtitle {
  font-size: 2.25rem;
  margin-bottom: 0.9375rem;
  background-image: linear-gradient(var(--cherry), var(--aqua));
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
}

@keyframes glitch {
  0% {
    transform: translate(0);
  }
  20% {
    transform: translate(-0.3125rem, 0.3125rem);
  }
  40% {
    transform: translate(-0.3125rem, -0.3125rem);
  }
  60% {
    transform: translate(0.3125rem, 0.3125rem);
  }
  80% {
    transform: translate(0.3125rem, -0.3125rem);
  }
  100% {
    transform: translate(0);
  }
}
#about__content p:not(:last-child) {
  margin: 1.5625rem 0;
}

.progress__box {
  min-width: 80%;
}
.progress__box span {
  display: inline-block;
  font-size: 1.625rem;
}
.progress__bar, .progress__bar span {
  min-width: 100%;
  border-radius: 0.625rem;
}
.progress__bar {
  position: relative;
  margin: 1.25rem 0;
  min-height: 1.25rem;
  background-color: var(--light-gray);
}
.progress__bar span {
  position: absolute;
  min-height: 100%;
  transform: scaleX(0);
  transform-origin: left;
  background-color: var(--aqua);
  box-shadow: 0 0 0.625rem var(--aqua);
  animation: progress var(--transition-min) forwards;
}
.progress__bar.html span {
  min-width: 90%;
}
.progress__bar.css span {
  min-width: 85%;
}
.progress__bar.javascript span {
  min-width: 70%;
}
.progress__bar.tailwind span {
  min-width: 75%;
}
.progress__bar.bootstrap span {
  min-width: 65%;
}
.progress__bar.git span {
  min-width: 80%;
}

@keyframes progress {
  100% {
    transform: scaleX(1);
  }
}
.hero {
  position: relative;
  min-height: 100vh;
}
@media (min-width: 71.875em) {
  .hero {
    display: flex;
    align-items: flex-end;
  }
}
.hero::before {
  content: "";
  position: absolute;
  left: -4.375rem;
  bottom: 3.625rem;
  min-height: 24.5rem;
  min-width: 39.875rem;
  transform: scaleX(-1) rotate(90deg);
  background: url("../images/console.webp") no-repeat 50%/contain;
}
@media (min-width: 40em) {
  .hero::before {
    left: 15.625rem;
  }
}
@media (min-width: 71.875em) {
  .hero::before {
    position: relative;
    left: 0;
    bottom: -2.03125rem;
    min-width: 46.125rem;
    transform: rotate(0);
  }
}
.hero::after {
  content: "";
  position: absolute;
  left: 6.25rem;
  bottom: 31.625rem;
  min-width: 31.25rem;
  min-height: 20.3125rem;
  transform: scaleX(-1);
  background: url("../images/avatar.webp") no-repeat 50%/contain;
}
@media (min-width: 40em) {
  .hero::after {
    left: 26.25rem;
  }
}
@media (min-width: 71.875em) {
  .hero::after {
    left: 26%;
    bottom: 15.625rem;
    min-height: 26.5625rem;
  }
}

.contact__title {
  margin-top: 100px;
  color: #fff;
  text-transform: uppercase;
  transition: all 4s ease-in-out;
}
.contact__title h1 {
  font-size: 32px;
  line-height: 10px;
}
.contact__title h2 {
  font-size: 16px;
}
.contact form {
  margin-top: 50px;
  transition: all 4s ease-in-out;
}
.contact form .submit {
  background: #ff5722;
  border-color: transparent;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
  letter-spacing: 2px;
  height: 50px;
  margin-top: 20px;
}
.contact form .submit:hover {
  background-color: #f44336;
  cursor: pointer;
}
.contact .form__control {
  width: 600px;
  background: transparent;
  border: none;
  outline: none;
  border-bottom: 1px solid gray;
  color: #fff;
  margin-bottom: 16px;
}
.contact input {
  height: 45px;
}

.portfolio {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}
@media (min-width: 71.875em) {
  .portfolio {
    max-height: 100vh;
  }
}
.portfolio__link {
  min-width: 100%;
  min-height: 15.625rem;
  overflow: hidden;
  flex-grow: 1;
}
@media (min-width: 40em) {
  .portfolio__link {
    min-width: 23.75rem;
  }
}
.portfolio__image, .portfolio .soon {
  background: no-repeat 50%/cover;
}
.portfolio__image {
  min-height: 100%;
  transition: var(--transition-min);
}
.portfolio__image:hover {
  transform: scale(1.2);
}
.portfolio__image.soon {
  cursor: url("../images/tools/unavailable.cur"), auto;
  background-image: url("https://via.placeholder.com/380x250?text=Coming+Soon");
}
.portfolio__image.item-1 {
  background-image: url("../images/content/item-1.webp");
}
.portfolio__image.item-2 {
  background-image: url("../images/content/item-2.webp");
}
.portfolio__image.item-3 {
  background-image: url("../images/content/item-3.webp");
}
.portfolio__image.item-4 {
  background-image: url("../images/content/item-4.webp");
}
.portfolio__image.item-5 {
  background-image: url("../images/content/item-5.webp");
}
.portfolio__image.item-6 {
  background-image: url("../images/content/item-6.webp");
}